import Model from '../Model/Model';
import View from '../View/View';
import Presenter from '../Presenter/Presenter';
import { DEFAULT_CONFIG } from '../defaults';

(function ($) {
  class SliderPlugin {
    constructor(options = DEFAULT_CONFIG) {
      this.model = new Model(options);
      this.view = new View(options);
      this.presenter = new Presenter(this.model, this.view);
      console.log(options);
      this.initApp();
    }

    initApp() {
      ;
      this.view.renderElements();
      this.view.addHandlers();
      this.presenter.subscribeListeners();
    }

  }

  $.fn.sliderPlugin = function (options) {
    options = $.extend({ ...DEFAULT_CONFIG,
      ...{
        target: this
      }
    }, options);
    return this.each(function () {
      if (!$.data(this, "sliderPlugin")) {
        $.data(this, "sliderPlugin", new SliderPlugin(options));
      }
    });
  };
})(jQuery);

let plug = $.fn.sliderPlugin;
export default plug;