class ViewContainer {
  constructor(slider) {
    if (slider) this.div = slider;else this.searchContainer();
    if (!this.div) this.createContainer();
  }

  searchContainer() {
    const container = document.querySelector("[data-slider]");

    if (container) {
      this.div = container;
    }
  }

  createContainer() {
    const sliderDiv = document.createElement("div");
    sliderDiv.classList.add("slider");
    document.body.append(sliderDiv);
    this.div = sliderDiv;
  }

}

export default ViewContainer;