import Observer from '../Observer/Observer';
import { DEFAULT_MODEL_CONFIG } from '../defaults';

class Model extends Observer {
  constructor(config = DEFAULT_MODEL_CONFIG) {
    super();
    this.init(config);
  }

  setElementsSize(data) {
    const {
      fieldSize = 0,
      buttonSize = 0
    } = data;
    this.elementsSize = {
      fieldSize: fieldSize - buttonSize,
      buttonSize
    };
  }

  calcShift(data) {
    const {
      buttonOffset,
      mouseCoords
    } = data;
    this.shift = mouseCoords - buttonOffset;
  }

  calcButtonOffset(data) {
    const {
      mouseCoords
    } = data;
    const {
      fieldSize
    } = this.elementsSize;
    const shiftLeft = mouseCoords - this.shift;
    if (shiftLeft >= fieldSize) this.updateButtonPX({ ...data,
      value: fieldSize
    });else if (shiftLeft <= 0) this.updateButtonPX({ ...data,
      value: 0
    });else this.updateButtonPX({ ...data,
      value: shiftLeft
    });
  }

  calcStopPointPX(data) {
    const {
      max,
      min,
      step
    } = this.config;
    const {
      buttonOffset
    } = data;
    const {
      fieldSize
    } = this.elementsSize;
    const stepPX = fieldSize * step / (max - min);
    const arrStopPoints = [];

    for (let i = 0; i <= fieldSize; i += stepPX) arrStopPoints.push(i);

    let stopPoint = arrStopPoints.find(item => buttonOffset <= item + stepPX / 2);
    if (stopPoint === undefined) stopPoint = fieldSize;
    this.updateButtonPX({ ...data,
      value: stopPoint
    });
  }

  calcFlagValue(data) {
    const {
      max,
      min
    } = this.config;
    const {
      buttonOffset
    } = data;
    const {
      fieldSize
    } = this.elementsSize;
    const part = min + buttonOffset * (max - min) / fieldSize;
    const digitsAfterDot = this.calcDigitsAfterDot();
    const fixedPart = Number(part.toFixed(digitsAfterDot));
    const result = this.findNearestValue(fixedPart);
    const roundedResult = Number(result.toFixed(this.calcDigitsAfterDot()));
    return roundedResult;
  }

  moveToValue(data) {
    const {
      value
    } = data;
    const {
      max,
      min
    } = this.config;
    const {
      fieldSize
    } = this.elementsSize;
    const result = fieldSize / (max - min) * (value - min);
    this.updateButtonPX({ ...data,
      value: result
    });
  }

  calcScaleValues(quantity) {
    const {
      max,
      min,
      step
    } = this.config;
    let modelQuantity = quantity;
    const quantityModelValues = (max - min) / step + 1;
    if (quantity < 3) modelQuantity = 2;
    if (quantityModelValues < quantity) modelQuantity = Number(quantityModelValues.toFixed(0));
    let value = min;
    const stepValues = (max - min) / (modelQuantity - 1);
    const digitsAfterDot = this.calcDigitsAfterDot();
    const stepValuesFixed = Number(stepValues.toFixed(digitsAfterDot));
    const arrValues = [];

    for (let i = 0; i < modelQuantity - 1; i += 1) {
      const result = this.findNearestValue(value);
      arrValues.push(Number(result.toFixed(digitsAfterDot)));
      value += stepValuesFixed;
    }

    arrValues.push(max);
    this.emit('scaleUpdate', {
      arrValues,
      quantity: modelQuantity
    });
  }

  init(config) {
    this.config = { ...DEFAULT_MODEL_CONFIG,
      ...config
    };
    this.validate();
    this.shift = 0;
  }

  validate() {
    let {
      max,
      min,
      step
    } = this.config;
    if (typeof max !== 'number' || max <= min) max = 100;
    if (typeof min !== 'number' || min >= max) min = 0;
    if (typeof step !== 'number' || step <= 0) step = 1;
    this.config = {
      max,
      min,
      step
    };
  }

  updateButtonPX(data) {
    const {
      button,
      value
    } = data;
    const flagValue = this.calcFlagValue(data);

    if (!button.previousElementSibling) {
      this.emit('updateButtonValue', flagValue);
      this.emit('updateButtonPX', value);
    } else {
      this.emit('updateButtonValue2', flagValue);
      this.emit('updateButtonPX2', value);
    }
  }

  calcDigitsAfterDot() {
    const {
      step
    } = this.config;
    const isIncludeDot = step.toString().includes('.');
    const digitsAfterDot = step.toString().split('.').pop().length;
    return isIncludeDot ? digitsAfterDot : 0;
  }

  findNearestValue(value) {
    const {
      max,
      min,
      step
    } = this.config;
    const stepsPoints = [];

    for (let i = min; i <= max; i += step) stepsPoints.push(i);

    let nearestValue = stepsPoints.find((item, index, array) => {
      const halfStepRes = value + step / 2;
      return halfStepRes >= item && halfStepRes < array[index + 1];
    });
    if (nearestValue === undefined) nearestValue = stepsPoints.pop();
    return nearestValue;
  }

}

export default Model;