class ViewButton {
  constructor(View) {
    this.init(View);
  }

  createButton() {
    this.div = document.createElement('div');
    this.div.className = 'js-slider__button';
    this.field.append(this.div);

    if (this.div.previousElementSibling) {
      this.div.style.left = `${this.div.previousElementSibling.offsetLeft + this.div.previousElementSibling.offsetWidth}px`;
    }

    if (!this.isHorizontal) {
      this.div.style.left = '-100%';

      if (this.div.previousElementSibling) {
        this.div.style.top = `${this.div.previousElementSibling.offsetTop + this.div.previousElementSibling.offsetHeight}px`;
      }
    }

    return this.div;
  }

  moveButton(value) {
    let result = value;
    if (this.isRangeSlider) result = this.demarcateButtons(value);
    if (this.isHorizontal) this.div.style.left = `${result}px`;
    if (!this.isHorizontal) this.div.style.top = `${result}px`;
  }

  init(View) {
    this.field = View.field.div;
    this.isHorizontal = View.config.isHorizontal;
    this.isRangeSlider = View.config.isRangeSlider;
  }

  demarcateButtons(value) {
    const {
      isHorizontal
    } = this;
    const {
      className
    } = this.div;
    const isButton = this.div.previousElementSibling;
    const isButton2 = this.div.nextElementSibling && this.div.nextElementSibling.classList.contains(className);

    if (isButton2) {
      let nextButtonOffset = this.div.nextElementSibling.offsetLeft - this.div.offsetWidth;

      if (!isHorizontal) {
        nextButtonOffset = this.div.nextElementSibling.offsetTop - this.div.offsetHeight;
      }

      if (value >= nextButtonOffset) return nextButtonOffset;
    }

    if (isButton) {
      let prevButtonOffset = this.div.previousElementSibling.offsetLeft + this.div.offsetWidth;

      if (!isHorizontal) {
        prevButtonOffset = this.div.previousElementSibling.offsetTop + this.div.offsetHeight;
      }

      if (value <= prevButtonOffset) return prevButtonOffset;
    }

    return value;
  }

}

export default ViewButton;